<template>
	<div id="getcash-detail">
		<public-header title="交易详情"></public-header>
		<div class="content">
			<div class="van-form-preview">
				<div class="van-form-preview__hd">
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">提现金额</div>
						<div class="van-form-preview__value_in-hd" :class="{'c-primary': getcash_log.status == 1, 'c-danger': getcash_log.status != 1}">¥{{getcash_log.get_fee}}</div>
					</div>
				</div>
				<div class="van-form-preview__bd">
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">订单号</div>
						<div class="van-form-preview__value">{{getcash_log.trade_no}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">提现金额</div>
						<div class="van-form-preview__value">{{getcash_log.get_fee}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">手续费</div>
						<div class="van-form-preview__value">{{getcash_log.take_fee}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">实际到账</div>
						<div class="van-form-preview__value">{{getcash_log.final_fee}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">申请时间</div>
						<div class="van-form-preview__value">{{getcash_log.addtime}}</div>
					</div>
					<div class="van-form-preview__item">
						<div class="van-form-preview__label">交易状态</div>
						<div class="van-form-preview__value">{{getcash_log.status_cn}}</div>
					</div>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				showPreLoading: true,
				getcash_log: {
					status: 0,
					get_fee: 0,
					trade_no: '',
					take_fee: 0,
					final_fee: 0,
					addtime: '',
					status_cn: ''
				}
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'delivery/finance/getcash/detail',
					autoAssign: true,
					variable: 'getcash_log',
					data: {
						id: that.id
					}
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>

</style>


